// Mixins

@mixin background-image-cover($image-name) {
  background-image: url('/assets/img/backgrounds/#{$image-name}');
  background-size: cover;
  background-position: top center;
}

/*
  This mixin can be used to set the object-fit:
  @include object-fit(contain);
  or object-fit and object-position:
  @include object-fit(cover, top);
*/
@mixin object-fit($fit: fill, $position: null){
  -o-object-fit: $fit;
     object-fit: $fit;
  @if $position {
    -o-object-position: $position;
       object-position: $position;
    font-family: 'object-fit: #{$fit}; object-position: #{$position}';
  } @else {
    font-family: 'object-fit: #{$fit}';
  }
}

/* Calculate fluid ratio based on two dimensions (width/height) */
@mixin fluid-ratio($large-size, $small-size) {
    $width-large: nth($large-size, 1);
    $width-small: nth($small-size, 1);
    $height-large: nth($large-size, 2);
    $height-small: nth($small-size, 2);
    $slope: ($height-large - $height-small) / ($width-large - $width-small);
    @if $height-large > $width-large {
        $slope: ($width-large - $width-small) / ($height-large - $height-small);
    }
    $width: $height-small - $width-small * $slope;
    @if $width-large > $height-large {
        $width: $width-small - $height-small * $slope;
    }

    padding-top: $slope * 100%;
    width: $width;

    background-size: cover;
    -moz-background-size: cover;  /* Firefox 3.6 */
    background-position: center;  /* Internet Explorer 7/8 */
}
