#header {
	background-color: $black;
	//@include background-image-cover('header-background.jpg');
	color: $white;
	padding-bottom: 10.7em;
	$map: (576px: 40px, 1200px: 171px);
	@include poly-fluid-sizing('padding-bottom', $map);
	position: relative;
	.vimeo-wrapper {
	   position: absolute;
	   top: 0;
	   bottom: 0;
	   left: 0;
	   width: 100%;
	   height: 100%;
	   z-index: 0;
	   pointer-events: none;
	   overflow: hidden;
	   #darken-video {
		   position: absolute;
		   background: $black;
		   opacity: .7;
		   width: 100%;
		   height: 100%;
	   }
	   iframe {
			display: block;
			width: 450%;
			height: 100%;
			margin-left: -175%;
			position: absolute;
			top: 0;
			bottom: 0;
		}
	}
	#banner {
		display: flex;
		padding: $default_spacing 0;
		width: 100%;
		@include medium {
			margin-top: 11.9em;
		}

		@include custom-max-width (500) {
			flex-direction: column;
		}
		#header-stick {
			@include medium {
				position: fixed;
			    left: 0;
			    top: 0;
			    z-index: 50;
			    right: 0;
			    max-width: 1440px;
				//background-color: rgba(0, 0, 0, 0.5);
				background-image: linear-gradient(to bottom, rgba(0,0,0,1) 0%,rgba(0,0,0,.6) 50%,rgba(0,0,0,0) 100%);
				//linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
			}
		}
		.logo * {
			transition: 0.6s all ease-in-out;
		}
		.logo {
			order: 2;
		}
		.headroom--not-top {
			&.slideDown {
				#header-stick-inner {
					max-height: 8.5em;
					overflow: hidden;
				}
			}
			.logo .logo-wrap {
				transition: 0.6s all ease-in-out;
				transform: scale(.7);
				transform-origin: top;
				span.logo-unknown {
					font-size: 1em;
					display: inline-block;
					//animation-name: logo-change-unknown;
					animation-duration: 4s;
					animation-iteration-count: 1;
					animation-fill-mode: forwards;
					transition: 0.6s all ease-in-out;
					margin-right: 0;
					left: 0;
					padding-bottom: 0;
				}
				span.logo-soldier {
					top: 0;
					padding: 0;
				}
				@keyframes logo-change-unknown {
					0% { left: 0em; top: 0em; display: block; }
					50% { left: 0em; top: 0em; }
					100% { display: inline-block; }
				}
				@keyframes logo-change-soldier {
					0% { left: 0em; top: 0em; display: block; }
					50% { left: 0em; top: 0em; }
					100% { display: inline-block; }
				}
				&:before {
					height: .28em;
					margin-bottom: 0.08em;
				}
				&:after {
					height: .6em;
				}
			}
		}

		#war-years {
			order: 1;
			margin: 2em 0 0;
			text-align: center;
			font-weight: bold;
			font-size: .95em;
			white-space: nowrap;
			@include custom-min-width (576) {
				text-align: left;
				margin-bottom: 2em;
			}
			&:before {
				@extend .sprite-icon-star;
				content: "";
				display: inline-block;
				color: $white;
				margin-bottom: -3px;
				margin-right: 12px;
			}
		}

		#open-nav {
			order: 3;
			text-transform: uppercase;
			margin: 0em 0 4em;
			text-align: center;
			font-weight: bold;
			font-size: .95em;
			letter-spacing: 2px;
			line-height: 2;
			white-space: nowrap;
			@include custom-min-width (576) {
				text-align: right;
				margin: 2em 0;
			}
			.hamburger-menu {
				margin: -21px 0 -16px 8px;
				@extend .sprite-icon-open-menu;
				display: inline-block;
				@include hide-text();
			}
		}
		@include small-tablet-portrait-and-larger {
			.logo, #war-years, #open-nav {
				flex-grow: 1;
			}
		}
	}

	h2 { // A closer look
		@extend %heading-content;
		$map: (576px: 42px, 1200px: 54px);
	    @include poly-fluid-sizing('padding-bottom', $map);
		width: 90%;
		position: relative;
		span.heading-opener {
			@extend %heading-opener;

			$map: (576px: 18px, 1200px: 21px);
		    @include poly-fluid-sizing('padding-bottom', $map);
		}
		span.heading-content {
			display: block;
			$map: (576px: 42px, 1200px: 56px);
		    @include poly-fluid-sizing('font-size', $map);
			@include large-desktop {
				width: 88%;
				margin: 0 auto;
			}
		}
	}
	.video-link {
		margin: 0 auto;
		@include only-xsmall {
			padding: 1em;
		}
	}
}
