// Full page menu
.full-page-overlay {
	height: 100%;
	width: 0;
	position: fixed;
	z-index: 51;
	top: 0;
	right: 0;
	background-color: rgb(255,255,255);
	overflow-x: hidden;
	transition: 0.5s;
	@include medium {
		background-image: url('/assets/img/backgrounds/episodes-background.jpg');
		background-position: 0 15%;
		background-size: cover;
	}
	@include desktop-and-larger {
		&:before {
			content: "";
			background-image: url('/assets/img/backgrounds/episodes-background-bottom-black.png');
			background-position: bottom center;
			background-repeat: no-repeat;
			display: block;
			width: 100%;
			height: 100%;
			position: fixed;
			bottom: 0;
			opacity: .7;
		}
	}

	.full-page-overlay-content {
		position: relative;
		top: 0%;
		width: 100%;
		text-align: center;
		margin-top: 30px;
	}
	.logo {
		margin: 0 auto 1em;
		color: $black;
	}

	p {
		color: $black;
		margin-top: .25em;
		text-transform: uppercase;
		&.copyright {
			font-family: $font-family-heading;
		    font-weight: 300;
			letter-spacing: 1px;
			margin-top: 3.5em;
			margin-bottom: 0;
			font-size: 130%;
		}
	}

	ul {
		a {
			font-family: $font-family-heading;
		    font-weight: 300;
			padding: 8px;
			text-decoration: none;
			font-size: 36px;
			color: #1D1D1D;
			display: block;
			transition: 0.3s;
		}
		list-style: none;
		margin: 0;
		padding: 0;
	}

	a:hover, a:focus {
		color: #000;
	}

	.closebtn {
		position: absolute;
		top: 20px;
		right: 45px;
		width: 1.15em;
		height: 1.15em;
		font-weight: bold;
		font-size: .95em;
		z-index: 20;
		span {
			position: absolute;
		    left: -100px;
		    top: 32px;
			font-size: 1.05em;
			color: $black;
		}
		&:before {
			content: "";
			width: 55px;
			height: 55px;
			display: block;
			position: absolute;
			right: 0;
			top: 1em;
			background-image: url("data:image/svg+xml,%3Csvg version='1.1' id='menu-close' xmlns='http://www.w3.org/2000/svg' x='0' y='0' viewBox='0 0 135.4 135.4' xml:space='preserve'%3E%3Cpath d='M97.1 37.1L67.4 66.8 37.7 37.1l.6 13.6 22.6 22.6-23.2 23.2 13.6-.6 16.1-16.1 16.1 16.1 13.6.6-23.2-23.2 22.7-22.6z'/%3E%3Cpath d='M67.7 3.5C32.2 3.5 3.5 32.2 3.5 67.7s28.7 64.2 64.2 64.2 64.2-28.7 64.2-64.2S103.1 3.5 67.7 3.5zm0 124.4c-33.2 0-60.2-27-60.2-60.2s27-60.2 60.2-60.2 60.2 27 60.2 60.2-27 60.2-60.2 60.2z'/%3E%3C/svg%3E");
			background-repeat: no-repeat;
		}
		&:hover {
			span {
				color: $red;
			}
			path {
				fill: red;
			}
		}
	}

	@media screen and (max-height: 450px) {
		 a {font-size: 20px}
		.closebtn {
			font-size: 40px;
			top: 15px;
			right: 35px;
		}
	}
}
