$theme: default;

// --------------------------------------------------
// Base
// --------------------------------------------------

.logo {
	font-family: $font-family-heading;
	.logo-wrap {
		$map: (300px: 35px, 575px: 55px, 576px: 27px, 600px: 26px, 1440px: 86px);
		@include poly-fluid-sizing('font-size', $map);
		letter-spacing: 0px;
		font-weight: 300;
		line-height: 0.8;
		margin: 0 0 .15em;
		display: inline-block;
		position: relative;
		overflow: visible;
		@include custom-min-max-width(576, 767) {
			margin-left: -15px;
			margin-right: -15px;
		}
		&:before {
			content: "";
			width: 100%;
			height: .12em;
			display: block;
			//border-top: solid .1em $white;
			margin-bottom: -0.1em;
			background-repeat: no-repeat;
			background-position: top center;
			background-image: url("data:image/svg+xml;base64,");
		}
		&:after {
			content: "";
			width: 100%;
			height: .18em;
			display: block;
			//border-bottom: solid .1em $white;
			transform: scaleY(.8);
			transform-origin: top;
			margin-top: 4px;
			background-repeat: no-repeat;
			background-position: top center;
			background-image: url("data:image/svg+xml;base64,");
		}
		span.logo-unknown {
			font-size: .76em;
			display: inline-block;
			line-height: .75;
			position: relative;
			left: .05em;
			padding-bottom: 1em;
			margin-right: -3.6em;
		}
		span.logo-soldier {
			font-size: 1em;
			display: inline-block;
			line-height: .75;
			position: relative;
			top: .75em;
			padding: 0 .05em;
		}
	}
}

h2, .h2 {
	@extend %main-title;
}
a {
	color: $red;
}

.distressed-still {
	background-color: $slate;
	height: 40em;
	$map: (500px: 219px, 1440px: 648px);
	@include poly-fluid-sizing('height', $map);
	overflow: hidden;
	-webkit-clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 0);
    clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 0);
	position: relative;
	& > div {
		background-repeat: no-repeat;
		background-size: cover;
		background-position: bottom center;
		position: fixed;
		bottom: 0;
		width: inherit;
		height: auto;
	}
	img {
		object-fit: contain;
		object-position: center top;
		opacity: 1;
		max-width: 100%;
		&.distressed-after {
			position: absolute;
			top: 0;
			left: 0;
		}
	}
	* {
		transition: 2s all ease-in-out;
	}
	&.release-image {
		background-size: cover;
		&  > div {
			position: relative;
		}
		img {
			opacity: 0;
		}
	}
}
